export interface ErrorData {
  keyword: string;
  dataPath?: string;
  schemaPath?: string;
  // tslint:disable-next-line:no-any
  params?: { [key: string]: any };
  message?: string;
  _custom?: boolean;
}

export interface ErrorSchema {
  /**
   * `true`
   * - `true` 
   * - `false` 
   */
  liveValidate?: boolean;
  /**
   *  `ErrorData.keyword` 
   */
  errors?: { [key: string]: string | ((obj: ErrorData) => string) };
  /**
   * `false`
   */
  firstVisual?: boolean;
  /**
   * `false`
   */
  onlyVisual?: boolean;
  /**
   *  `ERRORSDEFAULT`
   * -  `DelonSchemaFormConfig.ingoreKeywords`
   */
  ingoreKeywords?: string[];
  /**
   * 
   */
  validator?: (value: SFValue, formProperty: FormProperty, form: PropertyGroup) => ErrorData[] | Observable<ErrorData[]>;
}
